<!--// import ScoreDetails from '@/view/comment/score-details.vue'; //评分详情-->
// end完成
<template>
	<div class="DetailsScore" style="padding: 0 40px;">
		<h3 class="DetailsScore_title">iPhone X 的评分</h3>
		<div class="DetailsScore_score">
			<p class="DetailsScore_number">{{data.score}}</p>
			<ul class="DetailsScore_star">
				<li class="DetailsScore_star_li" v-for="item in 5">
					<img src="@/assets/comment-bar/in_star_light_icon@3x.png" alt="" class="DetailsScore_star_li_img"/>
				</li>
			</ul>
		</div>
		<p class="DetailsScore_scoreNumber">{{allCount}} 人评分</p>
		<div class="DetailsScore_scoreBar">
			<ul class="DetailsScore_scoreBar_ul">
				<li v-for="(item,index) in data.item" :key="index">
					<span class="DetailsScore_starLevel">{{item.score}}星</span>
					<div class="DetailsScore_barWrap">
						<img src="@/assets/comment-bar/score.png" :style="{ width:(item.count)/allCount*100+'%' }" alt=""/>
					</div>
					<span class="DetailsScore_percenter">{{(item.count)/allCount|Ratio(2)}}</span>
				</li>
			</ul>
		</div>
		<p class="DetailsScore_want" v-if="!data.mine">我想要</p>
		<p class="DetailsScore_want" v-else>我已拥有</p>
		<div class="DetailsScore_want_say" v-if="!data.mine">
			<span class="want_say_content">还没有入手这款手机，先说说外观…</span>
		</div>
		<div class="DetailsScore_starRating" v-else >
			 <ul class="DetailsScore_ul">
			 	  <li  class="DetailsScore_ul_li" v-for="(item,index) in 5">
						<img src="@/assets/comment-bar/in_star_null_icon@3x.png" alt=""  class="DetailsScore_darkStar" v-if="index+1 > data.mine"/>
						<img src="@/assets/comment-bar/editor_star_light_icon@2x.png" alt=""  class="DetailsScore_activeStar" v-else/>
			 	  </li>
			 </ul>
		</div>
		<p class="DetailsScore_revision" @click="next()">点击修改评价</p>
	</div>
</template>

<script>
import { nativeGo} from '@/assets/js/bridge.js'; // 引入桥
import { isApp } from '@/assets/js/env'
import appMixins from '@/assets/js/appMixins.js'
import StarCard from '@/components/star-card.vue'; //星级评分

export default {
	name:"details-score",
	components:{
    StarCard
  },
  mixins: [appMixins],
	data () {
    return {
			data:{
				item:[
					{score:5,count:80},
					{score:4,count:2},
					{score:3,count:5},
					{score:2,count:3},
					{score:1,count:2}
				],
				mine: 4,
				score: 4.7
			}
    }
  },
  computed:{
		allCount(){
			return  this.data.item.reduce((preV,curV,i,arr)=>{
				return preV+curV.count
			},0)
		},
	
	},
  methods: {
    next(){
			nativeGo('ratingModification',{native:true})
			if(!isApp){}
		}
  },
  mounted(){
		
  }
}
</script>

<style scoped lang="less">
	@m:750/375;
.DetailsScore{
	  width: 100%;
}	
.DetailsScore_title{
	  width: 100%;height: 28px*@m;
	  font-family: PingFangSC-Semibold;
		font-size: 28px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 28px*@m;
		text-align:center;
		margin-bottom: 30px*@m;
}
.DetailsScore_score{
	  width: 70px*@m;
	  height: 70px*@m;
	  background: #FFFFFF;
    box-shadow: 0 0 30px*@m 0 rgba(227,227,227,0.60);
    border-radius: 8px*@m;
    margin: 0 auto;
    text-align: center;
}
.DetailsScore_number{
	  font-family: PingFangHK-Medium;
		font-size: 30px*@m;
		color: #F7B519;
		letter-spacing: 0;
		line-height: 30px*@m;
		padding-top: 15px*@m;
}
.DetailsScore_star{
	 width: 100%;text-align: center;
	 margin-top:6px*@m;
	 height:8px*@m;
}
.DetailsScore_star_li{
	width: 8px*@m;
	height: 8px*@m;
	display: inline-block;
	margin-right: 4px*@m;
}
.DetailsScore_star_li:last-child{
	 margin-right: 0;
}
.DetailsScore_star_li_img{
	vertical-align: top;
}
.DetailsScore_scoreNumber{
	  font-family: PingFangHK-Regular;
		font-size: 13px*@m;
		color: #D7D7D7;
		letter-spacing: 0;
		text-align: center;
		line-height: 13px*@m;
		margin-top: 10px*@m;
		margin-bottom: 34px*@m;
}
.DetailsScore_scoreBar{
	width: 100%;
}
.DetailsScore_barWrap{
	float:left;
	display:inline-block;
	width:360px;
}
.DetailsScore_starLevel{
	font-family: PingFangHK-Regular;
	font-size: 13px*@m;
	color: #D7D7D7;
	letter-spacing: 0;
	text-align: center;
	width: 36px*@m;
	float: left;
}
.DetailsScore_scoreBar_ul li{
	height: 18px*@m;
	line-height: 18px*@m;
	text-align:center;
	margin-bottom: 12px*@m;
	margin-left: 40px*@m;
	position: relative;
}
.DetailsScore_scoreBar_ul img{
	float: left;
	width: 180px*@m;
	height: 10px*@m;
	margin-top: 3.5px*@m;
	margin-right: 10px*@m;
	border-radius: 100px*@m;
}
.DetailsScore_want{
	  font-family: PingFangSC-Medium;
		font-size: 14px*@m;
		color: #0F2540;
		letter-spacing: 0;
		line-height: 14px*@m;
		margin-top: 99px*@m;
		margin-bottom: 20px*@m;
		margin-left: 16px*@m;
}
.DetailsScore_want_say{
	width:calc(100% - 2px*@m);
	min-height: 70px*@m;
	background: #FFFFFF;
  border: 1px*@m solid #EFEFEF;
  border-radius: 6px*@m;
  text-align: center;
}
.DetailsScore_revision{
	font-family: PingFangHK-Regular;
	font-size: 13px*@m;
	color: #D7D7D7;
	letter-spacing: 0;
	text-align: center;
	line-height: 13px*@m;
	margin-top: 10px*@m;
}
.want_say_content{
	  font-family: PingFangSC-Regular;
		font-size: 18px*@m;
		color: #BCBCBC;
		letter-spacing: 0;
		line-height: 18px*@m;
		display: inline-block;
		margin-top: 26px*@m;
}
.DetailsScore_starRating{
	width:calc(100% - 2px*@m);
	height: 70px*@m;
	background: #FFFFFF;
  border: 1px*@m solid #EFEFEF;
  border-radius: 6px*@m;
  text-align: center;
}
.DetailsScore_ul{
	overflow: hidden;
}
.DetailsScore_ul_li{
	 display: inline-block;
	 margin-top: 22px*@m;
}
.DetailsScore_ul_li img{
	 vertical-align: top;
}
.DetailsScore_darkStar{
	width: 26px*@m;height: 26px*@m;
}
.DetailsScore_activeStar{
	width:43px*@m;height: 43px*@m;
}
.DetailsScore_percenter{
	font-family: PingFangHK-Regular;
	font-size: 13px*@m;
	color: #D7D7D7;
	letter-spacing: 0;
	position: absolute;
	right:43px*@m;
	top: 0;
	width: 26px*@m;
	display: inline-block;
	text-align:left;
}
	
	
</style>


