import Vue from 'vue'
import moment from 'moment'

// 数字*转百分比保留n位小数
Vue.filter('Ratio', function (oldV, param = 2) {
  return (+oldV * 100).toFixed(param) + '%'
})

// 时间格式化过滤
Vue.filter('dateFormat', function (value, type) { 
  return moment(value).format(type)
})



